* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
    
}

body {
    
    background-image: url('https://www.vlaminck14.be/uploads/files/slider/facebook_1539337036632.jpg');
    width: 100vw;
    height: 100vh;
    background-attachment: fixed;
    object-fit: cover;
    z-index: -100;
    
}


.form__container {
    margin: auto;
    background-color: #f1f1f1;
    border: 2px solid #ccc;
    width: 50vw;
    height: 600px;
    margin-top: 80px;
    opacity: 0.9;
    
}

.form__input {
    display: flex;
    padding: 10px 0px;
    flex-direction: column;
    margin-left: 80px;
    
}

.form__label {
    
    color:orange;
    text-shadow: 1px 1px 1px black;
    text-transform: capitalize;
    padding: 10px 0px;
}

.form__title {
   margin-top: 5vh;
   text-align: center;
   margin-bottom: -60px;
    color: orange;
    text-transform: capitalize;
    border-bottom: 2px solid rgba(17, 12, 2, 0.5);
    padding: 2px;

}


.form__btn {
    background-color: orange;
    color: rgb(20, 19, 19);
    font-weight: 800;
    width: 200px;
    height: 36px;
    box-shadow: 5px 5px 5px black;
    margin: auto;
}

.form__inputs {
    
    padding: 10vh;
}

.form__control {
    width: 30vw;
    height: 30px;
    border: 2px solid orange;
    border-radius: 5%;
}