.slide__container {
    width: 100%;
    height: 100vh;
    margin-top: 20vh;
    z-index: -100;
    position: relative;
    
}

.slider__1 {
    overflow: hidden;
    z-index: -100;
}
.slider__1 > img {
   position: absolute;
    background-repeat: no-repeat;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    margin-top: 0.1vh;
    z-index: -100;
    animation: slider1 4s linear infinite;
    animation-delay: 4s;
    overflow: hidden;
}

@keyframes slider1 {
    from {opacity: 0;}
   
    to {opacity: 0.8;
        transform: skew(2deg);
      
    }

}

 .slider__2 > img {
    position: absolute;
