.menu__container {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: auto auto auto;
    background-color:rgb(226, 226, 226);
    width: 80vw;
    height: 40vh;
    justify-content: center;
    margin:auto;
    margin-top: 5vh;
    border-radius: 15%;
    
}

.menu__item {
    width: 30vw;
    height: 30vh;
    background-color: #f1f1f1;
    margin-top: 5vh;
   
}
.menu__item> img {
    margin-top: 20px;
    width: 15vw;
    height: 15vh;
    object-fit: contain;
    display: flex;
    margin: auto;
    border-radius: 50%;
}

.menu__item > h2 {
    color: rgb(184, 100, 4);
    text-align: center;
    padding: 6px;
    font-size: 18px;
    margin: 10px 5px;
}

.menu__item > p {
    padding: 5px 20px;
    text-align: center;
    color: rgb(163, 89, 4);
}

.menu__item > .btn {
    width: 125px;
    height: 30px;
    background-color: orange;
    color: black;
    font-weight: 800;
    font-size: 18px;
    margin-left: 40%;
    margin-top: 3%;
    cursor: pointer;

}