* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.navbar {
    display: flex;
    height: 120px;
    background-color: white;
    box-shadow: 5px 5px 2px #ccc;
    position: sticky;

}

.logo > img {
    height: 100px;
    object-fit: contain;
}

.nav__links {
    list-style: none;
    display: flex;
    width: 60%;
    margin-left: auto;
    
}

.nav__link {
    display: flex;
    width: 8vw;
    margin-left: auto;
    margin-top: 7vh;
    margin-right: 2vw;
}

.nav__link > a {
    text-decoration: none;
    display: block;
    color: rgb(22, 21, 21);
    font-weight: 400;
    width: 160px;
    text-align: center;
    padding: 5px;
    text-transform: uppercase;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

    
}

.nav__link > a:hover {
    border-bottom: 3px solid orange;
    transition: all 200ms ease-in-out;
    color: orange;
}

