.opening__container {
    margin: auto;
    height: 600px;
    width: 50vw;
    margin-top: 70px;
    background-color: #ccc;
    opacity: 0.9;
}

.opening__container > h1 {
    color: orange;
    border-bottom: 1px solid orange;
    padding-top: 30px;
    text-align: center;
    padding-bottom: 10px;
    text-shadow: 1px 1px 1px black;
}



.table {
    width: 30vw;
    height: 5px;
}

.table > tr > th {
    color: orange;
    
}

.opening__heading {
    display: grid;
    grid-template-columns: 200px 250px;
    justify-content: center;
    
}

.opening__heading > ul {
    list-style: none;
    margin-top: 20px;
    
}

.opening__heading > ul > li {
    color: orange;
    
    border-bottom: 0.5px dotted white;
    padding: 10px;
    text-align: left;
    text-shadow: 1px 1px 1px rgb(48, 31, 1);
    
}
.opening__days {
    display: flex;
    justify-content: center;
    list-style: none;
}